import React, { useState } from 'react';
import { useEffect } from 'react';
import axios from 'axios';
import { useSelector, useDispatch } from 'react-redux';

function UpdateVm() {
  //servidor.
  var html = 'https://pulsarapp-server.herokuapp.com';
  // var html = '//localhost:3001';

  const changeState = useDispatch();
  const viewVm = useSelector((state) => state.updateVm);

  // login.
  const loginLogin = useSelector((state) => state.userlogin);
  const loginUsuario = useSelector((state) => state.username);
  const loginHospital = useSelector((state) => state.hospital);
  const loginCti = useSelector((state) => state.cti);
  const idpct = useSelector((state) => state.idpct);
  const gestor = useSelector((state) => state.gestor);

  // paciente.
  var box = useSelector((state) => state.box);
  var entrada = useSelector((state) => state.entrada);
  var nome = useSelector((state) => state.nome);
  var dn = useSelector((state) => state.dn);
  var hd = useSelector((state) => state.hd);
  var ap = useSelector((state) => state.ap);
  var alergias = useSelector((state) => state.alergias);
  var medprev = useSelector((state) => state.medprev);
  var exprev = useSelector((state) => state.exprev);
  var hda = useSelector((state) => state.hda);
  var status = useSelector((state) => state.status);
  var inter = useSelector((state) => state.inter);

  // invasões.
  var va = useSelector((state) => state.va);
  var jid = useSelector((state) => state.jid);
  var jie = useSelector((state) => state.jie);
  var subcld = useSelector((state) => state.subcld);
  var subcle = useSelector((state) => state.subcle);
  var radd = useSelector((state) => state.radd);
  var rade = useSelector((state) => state.rade);
  var vfemd = useSelector((state) => state.vfemd);
  var vfeme = useSelector((state) => state.vfeme);
  var afemd = useSelector((state) => state.afemd);
  var afeme = useSelector((state) => state.afeme);
  var svd = useSelector((state) => state.svd);
  var torax = useSelector((state) => state.torax);
  var abd = useSelector((state) => state.abd);
  var snc = useSelector((state) => state.snc);
  var dataVa = useSelector((state) => state.datava);
  var dataJid = useSelector((state) => state.datajid);
  var dataJie = useSelector((state) => state.datajie);
  var dataSubcld = useSelector((state) => state.datasubcld);
  var dataSubcle = useSelector((state) => state.datasubcle);
  var dataRadd = useSelector((state) => state.dataradd);
  var dataRade = useSelector((state) => state.datarade);
  var dataVfemd = useSelector((state) => state.datavfemd);
  var dataVfeme = useSelector((state) => state.datavfeme);
  var dataAfemd = useSelector((state) => state.dataafemd);
  var dataAfeme = useSelector((state) => state.dataafeme);
  var dataSvd = useSelector((state) => state.datasvd);
  var dataTorax = useSelector((state) => state.datatorax);
  var dataAbd = useSelector((state) => state.dataabd);
  var dataSnc = useSelector((state) => state.datasnc);

  // vm.
  var vm = useSelector((state) => state.vm);
  var pp = useSelector((state) => state.pp);
  var peep = useSelector((state) => state.peep);
  var fi = useSelector((state) => state.fi);

  // preenchendo campos.
  useEffect(() => {
    if (viewVm === 1) {
      document.getElementById('inputPeep').value = peep;
      document.getElementById('inputFi').value = fi;
      setVmselector(0);
    }
  }, [viewVm, vm, pp, peep, fi]);

  // atualizando registro.
  const [disabled, setDisabled] = useState(true);
  const sendData = () => {
    if (vmselector === 1) {
      vm = 'PCV';
      pp = document.getElementById('inputPp').value;
      peep = document.getElementById('inputPeep').value;
      fi = document.getElementById('inputFi').value;
      console.log('ID PCT: ' + idpct);
      console.log('VM: ' + vm);
      console.log('PP: ' + pp);
      console.log('PEEP: ' + peep);
      console.log('FI: ' + fi);
      saveData();
    } else if (vmselector === 2) {
      vm = 'VCV';
      pp = document.getElementById('inputPp').value;
      peep = document.getElementById('inputPeep').value;
      fi = document.getElementById('inputFi').value;
      saveData();
    } else {
      vm = 'PSV';
      pp = document.getElementById('inputPp').value;
      peep = document.getElementById('inputPeep').value;
      fi = document.getElementById('inputFi').value;
      saveData();
    }
  };

  const saveData = () => {
    if (pp !== '' && peep !== '' && fi !== '') {
      var obj = {
        cti: loginCti,
        box: box,
        entrada: entrada,
        nome: nome,
        dn: dn,
        hd: hd,
        ap: ap,
        alergias: alergias,
        medprev: medprev,
        exprev: exprev,
        hda: hda,
        status: status,
        va: va,
        jid: jid,
        jie: jie,
        subcld: subcld,
        subcle: subcle,
        radd: radd,
        rade: rade,
        vfemd: vfemd,
        vfeme: vfeme,
        afemd: afemd,
        afeme: afeme,
        svd: svd,
        torax: torax,
        abd: abd,
        snc: snc,
        datava: dataVa,
        datajid: dataJid,
        datajie: dataJie,
        datasubcld: dataSubcld,
        datasubcle: dataSubcle,
        dataradd: dataRadd,
        datarade: dataRade,
        datavfemd: dataVfemd,
        datavfeme: dataVfeme,
        dataafemd: dataAfemd,
        dataafeme: dataAfeme,
        datasvd: dataSvd,
        datatorax: dataTorax,
        dataabd: dataAbd,
        datasnc: dataSnc,
        vm: vm,
        pp: pp,
        peep: peep,
        fi: fi,
        inter: inter,
      };
      axios.post(html + '/updatepaciente/' + idpct, obj);
      toastSuccess();
    } else {
      document.getElementById('inputPp').value = '';
      document.getElementById('inputPeep').value = '';
      document.getElementById('inputFi').value = '';
      toastFail();
    }
  };

  const close = () => {
    changeState({
      type: 'CORRIDA_ON',
      payloadToastColor: toastColor,
      payloadToastMessage: toastMessage,
      payloadUserLogin: loginLogin,
      payloadUserName: loginUsuario,
      payloadHospital: loginHospital,
      payloadCti: loginCti,
      payloadGestor: gestor,
      payloadIdpct: idpct,
      payloadBox: box,
      payloadEntrada: entrada,
      payloadNome: nome,
      payloadDn: dn,
      payloadHd: hd,
      payloadAp: ap,
      payloadAlergias: alergias,
      payloadMedprev: medprev,
      payloadExprev: exprev,
      payloadHda: hda,
      payloadStatus: status,
      payloadVa: va,
      payloadJid: jid,
      payloadJie: jie,
      payloadSubcld: subcld,
      payloadSubcle: subcle,
      payloadRadd: radd,
      payloadRade: rade,
      payloadVfemd: vfemd,
      payloadVfeme: vfeme,
      payloadAfemd: afemd,
      payloadAfeme: afeme,
      payloadSvd: svd,
      payloadTorax: torax,
      payloadAbd: abd,
      payloadSnc: snc,
      payloadDatava: dataVa,
      payloadDatajid: dataJid,
      payloadDatajie: dataJie,
      payloadDatasubcld: dataSubcld,
      payloadDatasubcle: dataSubcle,
      payloadDataradd: dataRadd,
      payloadDatarade: dataRade,
      payloadDatavfemd: dataVfemd,
      payloadDatavfeme: dataVfeme,
      payloadDataafemd: dataAfemd,
      payloadDataafeme: dataAfeme,
      payloadDatasvd: dataSvd,
      payloadDatatorax: dataTorax,
      payloadDataabd: dataAbd,
      payloadDatasnc: dataSnc,
      payloadVm: vm,
      payloadPp: pp,
      payloadPeep: peep,
      payloadFi: fi,
      payloadInter: inter,
    });
  };

  // toast.
  var toastColor = '';
  var toastMessage = '';
  const toastSuccess = () => {
    toastColor = '#52BE80';
    toastMessage = 'PARÂMETROS VENTILATÓRIOS ATUALIZADOS';
    setTimeout(() => {
      changeState({
        type: 'TOAST_SUCCESS',
        payloadToastColor: toastColor,
        payloadToastMessage: toastMessage,
        payloadUserLogin: loginLogin,
        payloadUserName: loginUsuario,
        payloadHospital: loginHospital,
        payloadCti: loginCti,
        payloadGestor: gestor,
        payloadIdpct: idpct,
        payloadBox: box,
        payloadEntrada: entrada,
        payloadNome: nome,
        payloadDn: dn,
        payloadHd: hd,
        payloadAp: ap,
        payloadAlergias: alergias,
        payloadMedprev: medprev,
        payloadExprev: exprev,
        payloadHda: hda,
        payloadStatus: status,
        payloadVa: va,
        payloadJid: jid,
        payloadJie: jie,
        payloadSubcld: subcld,
        payloadSubcle: subcle,
        payloadRadd: radd,
        payloadRade: rade,
        payloadVfemd: vfemd,
        payloadVfeme: vfeme,
        payloadAfemd: afemd,
        payloadAfeme: afeme,
        payloadSvd: svd,
        payloadTorax: torax,
        payloadAbd: abd,
        payloadSnc: snc,
        payloadDatava: dataVa,
        payloadDatajid: dataJid,
        payloadDatajie: dataJie,
        payloadDatasubcld: dataSubcld,
        payloadDatasubcle: dataSubcle,
        payloadDataradd: dataRadd,
        payloadDatarade: dataRade,
        payloadDatavfemd: dataVfemd,
        payloadDatavfeme: dataVfeme,
        payloadDataafemd: dataAfemd,
        payloadDataafeme: dataAfeme,
        payloadDatasvd: dataSvd,
        payloadDatatorax: dataTorax,
        payloadDataabd: dataAbd,
        payloadDatasnc: dataSnc,
        payloadVm: vm,
        payloadPp: pp,
        payloadPeep: peep,
        payloadFi: fi,
        payloadInter: inter,
      });
    }, 500);
    setTimeout(() => {
      changeState({
        type: 'CORRIDA_ON',
        payloadUserLogin: loginLogin,
        payloadUserName: loginUsuario,
        payloadHospital: loginHospital,
        payloadCti: loginCti,
        payloadGestor: gestor,
        payloadIdpct: idpct,
        payloadBox: box,
        payloadEntrada: entrada,
        payloadNome: nome,
        payloadDn: dn,
        payloadHd: hd,
        payloadAp: ap,
        payloadAlergias: alergias,
        payloadMedprev: medprev,
        payloadExprev: exprev,
        payloadHda: hda,
        payloadStatus: status,
        payloadVa: va,
        payloadJid: jid,
        payloadJie: jie,
        payloadSubcld: subcld,
        payloadSubcle: subcle,
        payloadRadd: radd,
        payloadRade: rade,
        payloadVfemd: vfemd,
        payloadVfeme: vfeme,
        payloadAfemd: afemd,
        payloadAfeme: afeme,
        payloadSvd: svd,
        payloadTorax: torax,
        payloadAbd: abd,
        payloadSnc: snc,
        payloadDatava: dataVa,
        payloadDatajid: dataJid,
        payloadDatajie: dataJie,
        payloadDatasubcld: dataSubcld,
        payloadDatasubcle: dataSubcle,
        payloadDataradd: dataRadd,
        payloadDatarade: dataRade,
        payloadDatavfemd: dataVfemd,
        payloadDatavfeme: dataVfeme,
        payloadDataafemd: dataAfemd,
        payloadDataafeme: dataAfeme,
        payloadDatasvd: dataSvd,
        payloadDatatorax: dataTorax,
        payloadDataabd: dataAbd,
        payloadDatasnc: dataSnc,
        payloadVm: vm,
        payloadPp: pp,
        payloadPeep: peep,
        payloadFi: fi,
        payloadInter: inter,
      });
    }, 3500);
  };
  const toastFail = () => {
    toastColor = '#EC7063';
    toastMessage = 'PREENCHER CAMPOS EM BRANCO';
    changeState({
      type: 'TOAST_FAIL',
      // identificador do componente no qual o toast será renderizado.
      payloadUpdateVm: 1,
      payloadToastColor: toastColor,
      payloadToastMessage: toastMessage,
      // login.
      payloadUserLogin: loginLogin,
      payloadUserName: loginUsuario,
      payloadHospital: loginHospital,
      payloadCti: loginCti,
      payloadGestor: gestor,
      // paciente.
      payloadIdpct: idpct,
      payloadBox: box,
      payloadEntrada: entrada,
      payloadNome: nome,
      payloadDn: dn,
      payloadHd: hd,
      payloadAp: ap,
      payloadAlergias: alergias,
      payloadMedprev: medprev,
      payloadExprev: exprev,
      payloadHda: hda,
      payloadStatus: status,
      payloadVa: va,
      payloadJid: jid,
      payloadJie: jie,
      payloadSubcld: subcld,
      payloadSubcle: subcle,
      payloadRadd: radd,
      payloadRade: rade,
      payloadVfemd: vfemd,
      payloadVfeme: vfeme,
      payloadAfemd: afemd,
      payloadAfeme: afeme,
      payloadSvd: svd,
      payloadTorax: torax,
      payloadAbd: abd,
      payloadSnc: snc,
      payloadDatava: dataVa,
      payloadDatajid: dataJid,
      payloadDatajie: dataJie,
      payloadDatasubcld: dataSubcld,
      payloadDatasubcle: dataSubcle,
      payloadDataradd: dataRadd,
      payloadDatarade: dataRade,
      payloadDatavfemd: dataVfemd,
      payloadDatavfeme: dataVfeme,
      payloadDataafemd: dataAfemd,
      payloadDataafeme: dataAfeme,
      payloadDatasvd: dataSvd,
      payloadDatatorax: dataTorax,
      payloadDataabd: dataAbd,
      payloadDatasnc: dataSnc,
      payloadVm: vm,
      payloadPp: pp,
      payloadPeep: peep,
      payloadFi: fi,
      payloadInter: inter,
    });
    setTimeout(() => {
      changeState({
        type: 'TOAST_OFF',
        // identificador do componente no qual o toast será renderizado.
        payloadUpdateVm: 1,
        // login.
        payloadUserLogin: loginLogin,
        payloadUserName: loginUsuario,
        payloadHospital: loginHospital,
        payloadCti: loginCti,
        payloadGestor: gestor,
        // paciente.
        payloadIdpct: idpct,
        payloadBox: box,
        payloadEntrada: entrada,
        payloadNome: nome,
        payloadDn: dn,
        payloadHd: hd,
        payloadAp: ap,
        payloadAlergias: alergias,
        payloadMedprev: medprev,
        payloadExprev: exprev,
        payloadHda: hda,
        payloadStatus: status,
        payloadVa: va,
        payloadJid: jid,
        payloadJie: jie,
        payloadSubcld: subcld,
        payloadSubcle: subcle,
        payloadRadd: radd,
        payloadRade: rade,
        payloadVfemd: vfemd,
        payloadVfeme: vfeme,
        payloadAfemd: afemd,
        payloadAfeme: afeme,
        payloadSvd: svd,
        payloadTorax: torax,
        payloadAbd: abd,
        payloadSnc: snc,
        payloadDatava: dataVa,
        payloadDatajid: dataJid,
        payloadDatajie: dataJie,
        payloadDatasubcld: dataSubcld,
        payloadDatasubcle: dataSubcle,
        payloadDataradd: dataRadd,
        payloadDatarade: dataRade,
        payloadDatavfemd: dataVfemd,
        payloadDatavfeme: dataVfeme,
        payloadDataafemd: dataAfemd,
        payloadDataafeme: dataAfeme,
        payloadDatasvd: dataSvd,
        payloadDatatorax: dataTorax,
        payloadDataabd: dataAbd,
        payloadDatasnc: dataSnc,
        payloadVm: vm,
        payloadPp: pp,
        payloadPeep: peep,
        payloadFi: fi,
        payloadInter: inter,
      });
    }, 4000);
  };

  const fixPeep = () => {
    var y = '';
    y = document.getElementById('inputPeep').value;
    if (y.length > 2) {
      document.getElementById('inputPeep').value = '';
    }
  };

  const fixFi = () => {
    var z = ''; // definindo x como string, para que a propriedade lenght funcione.
    z = document.getElementById('inputFi').value;
    if (z.length > 3) {
      document.getElementById('inputFi').value = '';
    }
  };

  // validando entradas para campos numéricos.
  const validatePC = (txt) => {
    var last = txt.slice(-1);
    if (isNaN(last) === true) {
      last = '';
      document.getElementById('inputPp').value = '';
    } else {
    }
  };
  const validateVT = (txt) => {
    var last = txt.slice(-1);
    if (isNaN(last) === true) {
      last = '';
      document.getElementById('inputPp').value = '';
    } else {
    }
  };
  const validatePS = (txt) => {
    var last = txt.slice(-1);
    if (isNaN(last) === true) {
      last = '';
      document.getElementById('inputPp').value = '';
    } else {
    }
  };

  // definindo parâmetro ventilatório conforme a escolha do modo de controle:
  const [vmselector, setVmselector] = useState(0);
  function Parameter() {
    if (vmselector === 1) {
      return (
        <input
          autoComplete="off"
          className="input"
          placeholder="PC."
          onFocus={(e) => (e.target.placeholder = '')}
          onBlur={(e) => (e.target.placeholder = 'PC.')}
          onChange={(e) => validatePC(e.target.value)}
          title="PC (cmH20)."
          style={{
            width: 100,
            height: 50,
          }}
          maxLength={2}
          id="inputPp"
          defaultValue={pp}
        ></input>
      );
    } else if (vmselector === 2) {
      return (
        <input
          autoComplete="off"
          className="input"
          placeholder="VT."
          onFocus={(e) => (e.target.placeholder = '')}
          onBlur={(e) => (e.target.placeholder = 'VT.')}
          onChange={(e) => validateVT(e.target.value)}
          title="VT (ml)."
          style={{
            width: 100,
            height: 50,
          }}
          maxLength={4}
          id="inputPp"
          defaultValue={pp}
        ></input>
      );
    } else if (vmselector === 3) {
      return (
        <input
          autoComplete="off"
          className="input"
          placeholder="PS."
          onFocus={(e) => (e.target.placeholder = '')}
          onBlur={(e) => (e.target.placeholder = 'PS.')}
          onChange={(e) => validatePS(e.target.value)}
          title="PS (cmH20)."
          style={{
            width: 100,
            height: 50,
          }}
          maxLength={2}
          id="inputPp"
          defaultValue={pp}
        ></input>
      );
    } else {
      return null;
    }
  }

  // escolha do modo ventilatorio.
  const clickPCV = () => {
    setVmselector(1);
    setDisabled(false);
  };
  const clickVCV = () => {
    setVmselector(2);
    setDisabled(false);
  };
  const clickPSV = () => {
    setVmselector(3);
    setDisabled(false);
  };

  // renderização do componente.
  if (viewVm === 1) {
    return (
      <div
        className="secondary fade-in"
        style={{
          backgroundColor: 'rgba(0, 0, 0, 0.6)',
          borderRadius: 0,
          position: 'absolute',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
          height: '100%',
          width: '100%',
          marginTop: 0,
          marginBottom: 0,
          marginLeft: 0,
          marginRight: 0,
          zIndex: 3,
        }}
      >
        <div
          className="secondary"
          style={{
            backgroundColor: '#FFFFFF',
            borderRadius: 5,
            paddingTop: 30,
            paddingBottom: 30,
            paddingLeft: 30,
            paddingRight: 30,
          }}
        >
          <label className="title2" style={{ marginTop: 0, marginBottom: 15 }}>
            ATUALIZAR PARÂMETROS DA VM
          </label>

          <div
            style={{
              display: 'flex',
              flexDirection: 'column',
              borderRadius: 5,
              marginTop: 5,
              marginBottom: 0,
            }}
          >
            <label className="secondary" style={{ fontSize: 14 }}>
              MODO VENTILATORIO:
            </label>
            <div
              class="radio-toolbar"
              style={{ display: 'flex', flexDirection: 'row' }}
            >
              <input
                type="radio"
                id="radio1"
                name="mode"
                value="PCV"
                onClick={() => clickPCV()}
              ></input>
              <label for="radio1">PCV</label>
              <input
                type="radio"
                id="radio2"
                name="mode"
                value="VCV"
                onClick={() => clickVCV()}
              ></input>
              <label for="radio2">VCV</label>
              <input
                type="radio"
                id="radio3"
                name="mode"
                value="PSV"
                onClick={() => clickPSV()}
              ></input>
              <label for="radio3">PSV</label>
            </div>
          </div>
          <Parameter></Parameter>
          <label className="secondary" style={{ fontSize: 14 }}>
            PEEP:
          </label>
          <input
            autoComplete="off"
            className="input"
            placeholder="PEEP."
            onFocus={(e) => (e.target.placeholder = '')}
            onBlur={(e) => (e.target.placeholder = 'PEEP.')}
            onChange={() => fixPeep()}
            title="PEEP (cmH20)."
            style={{
              width: 100,
              height: 50,
            }}
            min={0}
            max={50}
            id="inputPeep"
          ></input>
          <label className="secondary" style={{ fontSize: 14 }}>
            FI:
          </label>
          <input
            autoComplete="off"
            className="input"
            placeholder="FI."
            onFocus={(e) => (e.target.placeholder = '')}
            onBlur={(e) => (e.target.placeholder = 'FI.')}
            onChange={() => fixFi()}
            title="FI (%)."
            style={{
              width: 100,
              height: 50,
            }}
            //type="number"
            min={0}
            max={100}
            id="inputFi"
          ></input>
          <div
            style={{
              display: 'flex',
              flexDirection: 'row',
              marginTop: 25,
              marginBottom: 0,
            }}
          >
            <button
              className="red-button"
              onClick={() => close()}
              style={{ width: 100 }}
            >
              CANCELAR
            </button>
            <button
              className="green-button"
              onClick={() => sendData()}
              disabled={disabled}
              style={{ width: 100, marginRight: 0 }}
            >
              SALVAR
            </button>
          </div>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default UpdateVm;
