        date1: '',
        date2: '',
        image: '',
        glasgow: '',
        rass: '',
        pa: '',
        pas: '',
        pad: '',
        fc: '',
        fr: '',
        sao2: '',
        tax: '',
        diu: '',
        fezes: '',
        bh: '',
        acv: '',
        ap: '',
        abdome: '',
        outros: '',
