import React from 'react';
import { useEffect } from 'react';
import axios from 'axios';
import { useSelector, useDispatch } from 'react-redux';
import deletar from '../images/deletar.svg';
import salvar from '../images/salvar.svg';

function UpdateInter() {
  //servidor.
  var html = 'https://pulsarapp-server.herokuapp.com';
  // var html = '//localhost:3001';

  const changeState = useDispatch();
  const viewInter = useSelector((state) => state.updateInter);

  // login.
  const loginLogin = useSelector((state) => state.userlogin);
  const loginUsuario = useSelector((state) => state.username);
  const loginHospital = useSelector((state) => state.hospital);
  const loginCti = useSelector((state) => state.cti);
  const idpct = useSelector((state) => state.idpct);
  const gestor = useSelector((state) => state.gestor);

  // paciente.
  var box = useSelector((state) => state.box);
  var entrada = useSelector((state) => state.entrada);
  var nome = useSelector((state) => state.nome);
  var dn = useSelector((state) => state.dn);
  var hd = useSelector((state) => state.hd);
  var ap = useSelector((state) => state.ap);
  var alergias = useSelector((state) => state.alergias);
  var medprev = useSelector((state) => state.medprev);
  var exprev = useSelector((state) => state.exprev);
  var hda = useSelector((state) => state.hda);
  var status = useSelector((state) => state.status);
  var inter = useSelector((state) => state.inter);

  // invasões.
  var va = useSelector((state) => state.va);
  var jid = useSelector((state) => state.jid);
  var jie = useSelector((state) => state.jie);
  var subcld = useSelector((state) => state.subcld);
  var subcle = useSelector((state) => state.subcle);
  var radd = useSelector((state) => state.radd);
  var rade = useSelector((state) => state.rade);
  var vfemd = useSelector((state) => state.vfemd);
  var vfeme = useSelector((state) => state.vfeme);
  var afemd = useSelector((state) => state.afemd);
  var afeme = useSelector((state) => state.afeme);
  var svd = useSelector((state) => state.svd);
  var torax = useSelector((state) => state.torax);
  var abd = useSelector((state) => state.abd);
  var snc = useSelector((state) => state.snc);
  var dataVa = useSelector((state) => state.datava);
  var dataJid = useSelector((state) => state.datajid);
  var dataJie = useSelector((state) => state.datajie);
  var dataSubcld = useSelector((state) => state.datasubcld);
  var dataSubcle = useSelector((state) => state.datasubcle);
  var dataRadd = useSelector((state) => state.dataradd);
  var dataRade = useSelector((state) => state.datarade);
  var dataVfemd = useSelector((state) => state.datavfemd);
  var dataVfeme = useSelector((state) => state.datavfeme);
  var dataAfemd = useSelector((state) => state.dataafemd);
  var dataAfeme = useSelector((state) => state.dataafeme);
  var dataSvd = useSelector((state) => state.datasvd);
  var dataTorax = useSelector((state) => state.datatorax);
  var dataAbd = useSelector((state) => state.dataabd);
  var dataSnc = useSelector((state) => state.datasnc);

  // vm.
  var vm = useSelector((state) => state.vm);
  var pp = useSelector((state) => state.pp);
  var peep = useSelector((state) => state.peep);
  var fi = useSelector((state) => state.fi);

  // preenchendo campos.
  useEffect(() => {
    if (viewInter === 1) {
      document.getElementById('inputInter').value = inter;
    }
  }, [viewInter, inter]);

  // atualizando registro.
  const sendData = () => {
    inter = document.getElementById('inputInter').value;
    if (inter === '') {
      inter = 'NÃO';
    }
    var obj = {
      cti: loginCti,
      box: box,
      entrada: entrada,
      nome: nome,
      dn: dn,
      hd: hd,
      ap: ap,
      alergias: alergias,
      medprev: medprev,
      exprev: exprev,
      hda: hda,
      status: status,
      va: va,
      jid: jid,
      jie: jie,
      subcld: subcld,
      subcle: subcle,
      radd: radd,
      rade: rade,
      vfemd: vfemd,
      vfeme: vfeme,
      afemd: afemd,
      afeme: afeme,
      svd: svd,
      torax: torax,
      abd: abd,
      snc: snc,
      datava: dataVa,
      datajid: dataJid,
      datajie: dataJie,
      datasubcld: dataSubcld,
      datasubcle: dataSubcle,
      dataradd: dataRadd,
      datarade: dataRade,
      datavfemd: dataVfemd,
      datavfeme: dataVfeme,
      dataafemd: dataAfemd,
      dataafeme: dataAfeme,
      datasvd: dataSvd,
      datatorax: dataTorax,
      dataabd: dataAbd,
      datasnc: dataSnc,
      vm: vm,
      pp: pp,
      peep: peep,
      fi: fi,
      inter: inter,
    };
    axios.post(html + '/updatepaciente/' + idpct, obj);
    toastSuccess();
  };

  const close = () => {
    changeState({
      type: 'CORRIDA_ON',
      payloadToastColor: toastColor,
      payloadToastMessage: toastMessage,
      payloadUserLogin: loginLogin,
      payloadUserName: loginUsuario,
      payloadHospital: loginHospital,
      payloadCti: loginCti,
      payloadGestor: gestor,
      payloadIdpct: idpct,
      payloadBox: box,
      payloadEntrada: entrada,
      payloadNome: nome,
      payloadDn: dn,
      payloadHd: hd,
      payloadAp: ap,
      payloadAlergias: alergias,
      payloadMedprev: medprev,
      payloadExprev: exprev,
      payloadHda: hda,
      payloadStatus: status,
      payloadVa: va,
      payloadJid: jid,
      payloadJie: jie,
      payloadSubcld: subcld,
      payloadSubcle: subcle,
      payloadRadd: radd,
      payloadRade: rade,
      payloadVfemd: vfemd,
      payloadVfeme: vfeme,
      payloadAfemd: afemd,
      payloadAfeme: afeme,
      payloadSvd: svd,
      payloadTorax: torax,
      payloadAbd: abd,
      payloadSnc: snc,
      payloadDatava: dataVa,
      payloadDatajid: dataJid,
      payloadDatajie: dataJie,
      payloadDatasubcld: dataSubcld,
      payloadDatasubcle: dataSubcle,
      payloadDataradd: dataRadd,
      payloadDatarade: dataRade,
      payloadDatavfemd: dataVfemd,
      payloadDatavfeme: dataVfeme,
      payloadDataafemd: dataAfemd,
      payloadDataafeme: dataAfeme,
      payloadDatasvd: dataSvd,
      payloadDatatorax: dataTorax,
      payloadDataabd: dataAbd,
      payloadDatasnc: dataSnc,
      payloadVm: vm,
      payloadPp: pp,
      payloadPeep: peep,
      payloadFi: fi,
      payloadInter: inter,
    });
  };

  // toast.
  var toastColor = '';
  var toastMessage = '';
  const toastSuccess = () => {
    toastColor = '#52BE80';
    toastMessage = 'CONTROLE DE INTERCONSULTAS ATUALIZADO';
    setTimeout(() => {
      changeState({
        type: 'TOAST_SUCCESS',
        payloadToastColor: toastColor,
        payloadToastMessage: toastMessage,
        payloadUserLogin: loginLogin,
        payloadUserName: loginUsuario,
        payloadHospital: loginHospital,
        payloadCti: loginCti,
        payloadGestor: gestor,
        payloadIdpct: idpct,
        payloadBox: box,
        payloadEntrada: entrada,
        payloadNome: nome,
        payloadDn: dn,
        payloadHd: hd,
        payloadAp: ap,
        payloadAlergias: alergias,
        payloadMedprev: medprev,
        payloadExprev: exprev,
        payloadHda: hda,
        payloadStatus: status,
        payloadVa: va,
        payloadJid: jid,
        payloadJie: jie,
        payloadSubcld: subcld,
        payloadSubcle: subcle,
        payloadRadd: radd,
        payloadRade: rade,
        payloadVfemd: vfemd,
        payloadVfeme: vfeme,
        payloadAfemd: afemd,
        payloadAfeme: afeme,
        payloadSvd: svd,
        payloadTorax: torax,
        payloadAbd: abd,
        payloadSnc: snc,
        payloadDatava: dataVa,
        payloadDatajid: dataJid,
        payloadDatajie: dataJie,
        payloadDatasubcld: dataSubcld,
        payloadDatasubcle: dataSubcle,
        payloadDataradd: dataRadd,
        payloadDatarade: dataRade,
        payloadDatavfemd: dataVfemd,
        payloadDatavfeme: dataVfeme,
        payloadDataafemd: dataAfemd,
        payloadDataafeme: dataAfeme,
        payloadDatasvd: dataSvd,
        payloadDatatorax: dataTorax,
        payloadDataabd: dataAbd,
        payloadDatasnc: dataSnc,
        payloadVm: vm,
        payloadPp: pp,
        payloadPeep: peep,
        payloadFi: fi,
        payloadInter: inter,
      });
    }, 500);
    setTimeout(() => {
      changeState({
        type: 'CORRIDA_ON',
        payloadUserLogin: loginLogin,
        payloadUserName: loginUsuario,
        payloadHospital: loginHospital,
        payloadCti: loginCti,
        payloadGestor: gestor,
        payloadIdpct: idpct,
        payloadBox: box,
        payloadEntrada: entrada,
        payloadNome: nome,
        payloadDn: dn,
        payloadHd: hd,
        payloadAp: ap,
        payloadAlergias: alergias,
        payloadMedprev: medprev,
        payloadExprev: exprev,
        payloadHda: hda,
        payloadStatus: status,
        payloadVa: va,
        payloadJid: jid,
        payloadJie: jie,
        payloadSubcld: subcld,
        payloadSubcle: subcle,
        payloadRadd: radd,
        payloadRade: rade,
        payloadVfemd: vfemd,
        payloadVfeme: vfeme,
        payloadAfemd: afemd,
        payloadAfeme: afeme,
        payloadSvd: svd,
        payloadTorax: torax,
        payloadAbd: abd,
        payloadSnc: snc,
        payloadDatava: dataVa,
        payloadDatajid: dataJid,
        payloadDatajie: dataJie,
        payloadDatasubcld: dataSubcld,
        payloadDatasubcle: dataSubcle,
        payloadDataradd: dataRadd,
        payloadDatarade: dataRade,
        payloadDatavfemd: dataVfemd,
        payloadDatavfeme: dataVfeme,
        payloadDataafemd: dataAfemd,
        payloadDataafeme: dataAfeme,
        payloadDatasvd: dataSvd,
        payloadDatatorax: dataTorax,
        payloadDataabd: dataAbd,
        payloadDatasnc: dataSnc,
        payloadVm: vm,
        payloadPp: pp,
        payloadPeep: peep,
        payloadFi: fi,
        payloadInter: inter,
      });
    }, 3500);
  };

  // renderização do componente.
  if (viewInter === 1) {
    return (
      <div
        className="secondary fade-in"
        style={{
          backgroundColor: 'rgba(0, 0, 0, 0.6)',
          borderRadius: 0,
          position: 'absolute',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
          height: '100%',
          width: '100%',
          marginTop: 0,
          marginBottom: 0,
          marginLeft: 0,
          marginRight: 0,
          zIndex: 3,
        }}
      >
        <div
          className="secondary"
          style={{
            backgroundColor: '#FFFFFF',
            borderRadius: 5,
            paddingTop: 30,
            paddingBottom: 30,
            paddingLeft: 30,
            paddingRight: 30,
          }}
        >
          <label className="title2" style={{ marginTop: 0, marginBottom: 15 }}>
            ATUALIZAR INTERCONSULTAS
          </label>
          <textarea
            autoComplete="off"
            className="textarea"
            placeholder="ESCREVA AQUI AS ESPECIALIDADES PARA INTERCONSULTA."
            onFocus={(e) => (e.target.placeholder = '')}
            onBlur={(e) =>
              (e.target.placeholder =
                'ESCREVA AQUI AS ESPECIALIDADES PARA INTERCONSULTA.')
            }
            title="INTERCONSULTAS."
            style={{
              width: 300,
              height: 100,
            }}
            type="text"
            id="inputInter"
          ></textarea>
          <div
            style={{
              display: 'flex',
              flexDirection: 'row',
              marginTop: 25,
              marginBottom: 0,
            }}
          >
            <button className="red-button" onClick={() => close()}>
              <img
                alt=""
                src={deletar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
            <button className="green-button" onClick={() => sendData()}>
              <img
                alt=""
                src={salvar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
          </div>
        </div>
      </div>
    );
  } else {
    return null;
  }
}

export default UpdateInter;
